
.parts-container {  
    height: 90vh;
    width: 100vw;
    justify-items: center;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-auto-columns: 1fr;
    gap: 0.1%;
    grid-auto-flow: row;
    grid-template-areas:
    "about about about about experience experience"
    "about about about about experience experience"
    "about about about about experience experience"
    "tech tech tech cv cv cv"
    "tech tech tech cv cv cv"
    "tech tech tech cv cv cv"; 
  }
  
.about {
  display: flex;
  grid-area: about;
  height: 90%;
  width: 95%;
}

.tech {
  
  grid-area: tech;
  height: 90%;
  width: 95%;
  
}

.experience {
  grid-area: experience;
  height: 90%;
  width: 95%;
  
}

.cv {
  grid-area: cv;
  height: 90%;
  width: 95%;
  
}

.about-talk img {
  height: 12%;
  width: 12%;
}
.about-talk p{
  font-size: 24px;
  line-height: 2.2rem;
  margin: 2%;
}

.about-talk h2{
  margin: 2%;
  font-size: 48px;
}

.gradient-text {
  background-color: #f3ec78;
  background-image: linear-gradient(45deg, #1A6DFF, #C822FF);
  background-size: 100%;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent; 
  -moz-text-fill-color: transparent;
}

.gradient-header {
  text-align: center;
  margin-top: 1rem;
  font-weight: 800;
  font-size: 28px;
  
}

.skills-container {  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-auto-columns: 1fr;
  gap: 1em 1em;
  grid-auto-flow: row;
  justify-items: stretch;
  grid-template-areas:
    ". . . . ."
    "research research . Music Music"
    ". Analyse Analyse User-Experience User-Experience"
    "Simplicity Simplicity . minimalistic minimalistic"
    "Coffee Coffee . minimalistic minimalistic"
    "Coffee Coffee . . ."
    ". Responsive-Web-Design Responsive-Web-Design Responsive-Web-Design ."
    ". Responsive-Web-Design Responsive-Web-Design Responsive-Web-Design ."
    ". . . . ."
    ". . . . .";
}

.research { grid-area: research; }

.Analyse { grid-area: Analyse; }

.User-Experience { grid-area: User-Experience; }

.Coffee { grid-area: Coffee; }

.minimalistic { grid-area: minimalistic; }

.Simplicity { grid-area: Simplicity; }

.Responsive-Web-Design { grid-area: Responsive-Web-Design; }

.Music { grid-area: Music; }




@media screen and (max-width: 810px) {

  h2 {
    font-size: 18px;
  }
  .about-talk p{
    font-size: 18px;
    line-height: 1.5rem;
    margin: 2%;
  }
  
  
  
  
  .about-talk h2{
    margin: 2%;
    font-size: 28px;
  }
}




