body {
  background-color: #141414;
}
.landing_page {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
}

/*                                             Catch Line Section                                                           */
.landing_page h1 {
  color: #8685ef;
  font-size: 64px;
  font-weight: 100;
  line-height: 1.5;
  text-transform: uppercase;
  white-space: nowrap;
  overflow: hidden;
  position: relative;
  width: 100%;
  text-align: left;
}

h1 span {
  font-size: 64px;
}

.catch_line {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex: 50%;
}

.catch_line p {
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  color: #faf8ff;
  margin: 1rem 4rem;
}

.catch_line h1 {
  font-style: normal;
  font-weight: 700;
  font-size: 64px;
  margin: 0.5rem 4rem;
  color: #8685ef;
}

.mj {
  font-weight: 700;
  color: #8685ef;
}

/* catch line section animation                */

.animated_text {
  color: #8685ef;
  display: block;
  font-weight: 700;
  overflow: hidden;
  position: absolute;
  padding-left: 0.5rem;
  top: 0.2rem;
  left: 1rem;
  animation: openclose 6s ease-in-out forwards;
}

@keyframes openclose {
  0% {
    top: 0rem;
    width: 0;
  }
  5% {
    width: 0;
  }
  15% {
    width: 230px;
  }
  30% {
    top: 0rem;
    width: 230px;
  }
  33% {
    top: 0rem;
    width: 0;
  }
  35% {
    top: -6rem;
    width: 0;
  }
  38% {
    top: -6rem;
  }
  48% {
    top: -6rem;
    width: 280px;
  }
  62% {
    top: -6rem;
    width: 280px;
  }
  66% {
    top: -6rem;
    width: 0;
    text-indent: 0;
  }
  71% {
    top: -12rem;
    width: 0;
    text-indent: 5px;
  }
  86% {
    top: -12rem;
    width: 285px;
  }
  100% {
    top: -12rem;
    width: 285px;
  }
}

/* Sections Section                       */

.sections_grid {
  height: 75%;
  width: 50%;
  justify-items: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
  grid-auto-columns: 1fr;
  gap: 1em 1em;
  grid-auto-flow: row;
  justify-items: stretch;
  grid-template-areas:
    "work work work work about about about about"
    "work work work work about about about about"
    "work work work work about about about about"
    "work work work work get get more more"
    "work work work work get get more more"
    "work work work work get get more more";
}

.work {
  grid-area: work;

  height: 95%;
  width: 95%;
}

.about {
  grid-area: about;
  height: 90%;
  width: 95%;
}

.get {
  grid-area: get;
  height: 90%;
  width: 90%;
}

.more {
  grid-area: more;
  height: 90%;
  width: 90%;
}

.sections_grid div {
  height: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
}


/* reponsiveness section                                           */
@media screen and (max-width: 1000px) {
  .landing_page {
    flex-direction: column;
  }

  .sections_grid {
    width: 80%;
  }
}

@media screen and (max-width: 700px) {
  .sections_grid {  height: 75%;
    width: 80%;
    justify-items: center;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-auto-columns: 1fr;
    gap: 1em 1em;
    grid-auto-flow: row;
    justify-items: stretch;
    grid-template-areas:
      "work work work work about about about about"
      "work work work work about about about about"
      "work work work work about about about about"
      "work work work work get get get get"
      "work work work work get get get get"
      "work work work work more more more more"
      "work work work work more more more more";
  }
  
  .about { grid-area: about; }
  
  .work { grid-area: work; }
  
  .get { grid-area: get; }
  
  .more { grid-area: more; }
  

  .get {
    grid-area: get;
    
    width: 95%;
  }

  .more {
    grid-area: more;
    height: 80%;
    width: 95%;
  }
}
