.projects-container {  
    height: 90vh;
    width: 100vw;
    justify-items: center;
    align-items: center;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-auto-columns: 1fr;
    gap: 0.1%;
    grid-auto-flow: row;
    grid-template-areas:
      "project1 project1 project1 project1 project2 project2 project2 project2"
      "project1 project1 project1 project1 project2 project2 project2 project2"
      "project1 project1 project1 project1 project2 project2 project2 project2"
      "project1 project1 project1 project1 project3 project3 project4 project4"
      "project1 project1 project1 project1 project3 project3 project4 project4"
      "project1 project1 project1 project1 project3 project3 project4 project4";
  }
  
  .project1 { grid-area: project1;
    height: 95%;
    width: 95%; }
  
  .project2 { grid-area: project2; 
    height: 90%;
    width: 95%;}
  
  .project3 { grid-area: project3;
    height: 90%;
    width: 90%; }
  
  .project4 { grid-area: project4;
    height: 90%;
    width: 90%; }
  

    .center{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 100%;

      
    }

    .center p {
      margin-top: 1rem;
      width: 70%;
      text-align: center;
    }

    img {
      height: 90%;
      width: 50%;
      margin: 1.5em 2em;
    }